.App{
    position: relative;
    width: 100%;
    height: 98vh; 
    border-collapse: collapse;
    
}
body{
    overflow: hidden;
}


*{
    -webkit-user-drag: none;
    box-sizing: border-box;
}


.mainBackground{
    position: fixed;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
object-fit: cover;
}